.button{
     --accent-color: blueviolet;
    background-color: var(--accent-color);
    border: none;
    color: aliceblue;
    padding: 8px 16px;
    border-radius: 8px;
    text-align: center;
    text-decoration: none;
    display: inline-block;
    font-size: 16px;
    cursor: pointer;
}
.secondary{
    background-color: transparent;
    border: 1px solid var(--accent-color);
    color: var(--accent-color);
}
.tertiary{
    background-color: transparent;
    border: transparent;
    color: var(--accent-color);
}